import React, { useState, useEffect } from 'react';
import Helmet from 'react-helmet';
import queryString from 'query-string';

import Layout from '../components/layout';
import PageNavigation from '../components/page-navigation';
import Lightbox from '../components/lightbox';

import { makeStyles } from '@material-ui/core/styles';
import { Accordion, AccordionSummary, AccordionDetails, Typography } from '@material-ui/core';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';

import {
  TwitterShareButton, TwitterIcon,
  FacebookShareButton, FacebookIcon,
  LineShareButton, LineIcon
} from 'react-share';

const useStyles = makeStyles(theme => ({
  heading: {
    fontSize: theme.typography.pxToRem(15),
    fontWeight: theme.typography.fontWeightBold,
  }
}));

export default () => {
  const [contents, setContents] = useState(null);

  const hashedAnswer = typeof window === 'undefined'
                        ? null
                        : queryString.parse(window.location.search).hashedAnswer;

  useEffect(() => {
    if (hashedAnswer !== null) {
      fetch(
        `https://plme53il46.execute-api.ap-northeast-1.amazonaws.com/Prod/answer?hashedAnswer=${hashedAnswer}`,
        { cache: 'no-cache' }
      ).then(response => response.json()).then(setContents);
    }
  }, [hashedAnswer]);

  const classes = useStyles();

  return contents === null ? <Helmet title="最後のページ | 氷蔵姉妹の挑戦" /> : (
    <Layout>
      <Helmet title={`${contents.title} | 氷蔵姉妹の挑戦`} />
        <section id="header">
          <div className="inner">
            <h1>{contents.heading}</h1>
          </div>
        </section>

        <section id="one" className="main style1">
          <div className="grid-wrapper">
            <div className="col-12">
              {
                contents.body.map((paragraph, index) => {
                  switch (paragraph.type) {
                    case "text":
                      return (
                        <span key={`text${index}`}>
                          {paragraph.text}<br/>
                        </span>
                      );
                    case "newline":
                      return <br key={`newline${index}`} />;
                    case "image":
                      return (
                        <Lightbox
                          key={`image${index}`}
                          src={paragraph.url}
                          fit={'fit' in paragraph ? paragraph.fit : true}
                        />
                      );
                    case "share":
                      return (
                        <div>
                          <div>結果をシェアする</div>
                          <TwitterShareButton url="https://reizouko.wixsite.com/ryoka-hikura/sister-challenge" title={paragraph.sentence} hashtags={["氷蔵姉妹の挑戦"]} style={{boxShadow: "none"}}>
                            <TwitterIcon size={48} round />
                          </TwitterShareButton>
                          <FacebookShareButton url="https://sister-challenge.reizouko.online/" quote={paragraph.sentence} hashtag="#氷蔵姉妹の挑戦" style={{boxShadow: "none"}}>
                            <FacebookIcon size={48} round />
                          </FacebookShareButton>
                          <LineShareButton url="https://sister-challenge.reizouko.online/" title={paragraph.sentence} style={{boxShadow: "none"}}>
                            <LineIcon size={48} round />
                          </LineShareButton>
                        </div>
                      );
                    case "accordion":
                      return (
                        <Accordion key={`accordion${index}`}>
                          <AccordionSummary
                            expandIcon={<ExpandMoreIcon />}
                            aria-controls="panel-content"
                          >
                            <Typography className={classes.heading}>{paragraph.title}</Typography>
                          </AccordionSummary>
                          <AccordionDetails>
                            <Typography>
                              {
                                paragraph.body.map((accordionContents, accordionIndex) => {
                                  switch (accordionContents.type) {
                                    case "text":
                                      return (
                                        <span key={`accordion${index}text${accordionIndex}`}>
                                          {accordionContents.text}<br/>
                                        </span>
                                      );
                                    case "newline":
                                      return <br key={`accordion${index}newline${accordionIndex}`}/>;
                                    case "image":
                                      return (
                                        <Lightbox
                                          key={`accordion${index}image${accordionIndex}`}
                                          src={accordionContents.url}
                                          fit={'fit' in accordionContents ? accordionContents.fit : true}
                                        />
                                      );
                                    default:
                                      return (
                                        <span key={`accordion${index}dummy${accordionIndex}`}></span>
                                      );
                                  }
                                })
                              }
                            </Typography>
                          </AccordionDetails>
                        </Accordion>
                      );
                    default:
                      return (
                        <span key={`dummy${index}`}></span>
                      );
                  }
                })
              }
            </div>
          </div>
          <br/>
          <div className="grid-wrapper">
            <div className="col-12">
              <PageNavigation prev="/question/" />
            </div>
          </div>
        </section>
    </Layout>
  );
};
